@charset "UTF-8";
@font-face {
  font-weight: normal;
  font-family: "Montserrat";
  font-style: normal;
  src: url("../fonts/Montserrat-Light.woff") format("woff"); }

@font-face {
  font-weight: bold;
  font-family: "Montserrat";
  font-style: normal;
  src: url("../fonts/Montserrat-Bold.woff") format("woff"); }

body {
  margin: 0 auto;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal; }

.button {
  display: block;
  font-size: 12px;
  line-height: 15px;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  font-weight: bold;
  width: 260px;
  align-items: center;
  letter-spacing: 2px;
  padding: 30px 40px 30px 40px;
  background-color: #fe7865; }

.logo {
  display: flex;
  align-self: center;
  margin-top: 18px;
  margin-bottom: 18px;
  padding: 2px 25px 0 15px;
  width: 100px; }

.page-header__wrapper {
  max-width: 1200px;
  min-height: 99px;
  margin: 0 auto;
  display: flex;
  flex-direction: row; }

.main-nav {
  display: flex;
  flex-grow: 1;
  justify-content: flex-end; }

.main-nav__list {
  display: flex; }

.site-list {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin: 0;
  padding: 0;
  list-style: none; }

.site-list__link {
  display: block;
  padding-top: 44px;
  padding-bottom: 35px;
  padding-left: 20px;
  padding-right: 20px;
  text-decoration: none;
  font-size: 14px;
  font-style: normal;
  line-height: 17px;
  font-weight: 500;
  color: #141517; }

.site-list__link[href]:hover {
  color: #fe7865; }

.site-list__link[href]:active {
  color: #fe7865; }

.info-contacts {
  display: flex; }

.info-contacts__tel {
  display: block;
  padding-top: 38px;
  padding-bottom: 30px;
  padding-left: 30px;
  padding-right: 30px;
  margin-left: 55px;
  font-size: 18px;
  line-height: 32px;
  color: #fe7865; }

.promo {
  min-height: 860px;
  /*цвет отредактировать*/
  background-image: url("../img/promo-desktop.jpg");
  background-repeat: no-repeat;
  background-position: center 0;
  background-size: auto 860px;
  margin-bottom: 155px; }

.promo__wrapper {
  max-width: 1200px;
  min-height: 860px;
  margin: 0 auto;
  display: flex;
  flex-grow: row;
  justify-content: flex-end;
  align-items: center; }

.promo__info {
  width: 40%;
  align-self: flex-start;
  margin-top: 15.5%; }

.promo__title {
  font-weight: 600;
  font-size: 55px;
  line-height: 80px;
  color: #ffffff;
  margin: 0; }

.promo__description {
  width: 72%;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 170%;
  color: #ffffff;
  margin-bottom: 40px; }

.page-main__wrapper {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column; }

.offers {
  display: flex;
  flex-wrap: wrap; }

.offers__info {
  display: flex;
  flex-direction: column;
  width: 100%; }

.offers__title {
  font-style: normal;
  font-weight: 600;
  font-size: 55px;
  line-height: 80px;
  color: #2c2e3f;
  width: 33%;
  margin-left: 15px;
  letter-spacing: -1.245px; }

.offers__description {
  width: 33%;
  font-weight: normal;
  font-size: 16px;
  line-height: 28px;
  margin-left: 15px;
  margin-bottom: 55px; }

.offers__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: 100%; }

.offers__item {
  position: relative;
  margin: auto;
  margin-bottom: 100px; }

.offers__name {
  position: absolute;
  bottom: 4px;
  padding: 16px 62px 16px 62px;
  right: 25%;
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  letter-spacing: -0.415px;
  background-color: #ffffff; }

.slider__wrapper {
  position: relative;
  display: flex;
  flex-direction: row;
  margin-top: 50px; }

.slider__toggle {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  color: rgba(44, 46, 63, 0.3);
  letter-spacing: -0.415px;
  text-align: center;
  border: 0;
  background-color: #ffffff; }

.slider__info {
  position: relative;
  width: 370px;
  margin-left: 10px; }

.slider__title {
  font-style: normal;
  font-weight: 600;
  font-size: 55px;
  line-height: 90px;
  letter-spacing: -1.245px;
  color: #2c2e3f; }

.slider__text {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 28px;
  letter-spacing: -0.466667px;
  color: rgba(44, 46, 63, 0.7); }

.slider__img {
  margin-top: 30px;
  margin-left: 100px; }

.slider__block {
  position: absolute;
  top: 150px;
  right: 20px; }

.review {
  width: 382px;
  min-height: 367px;
  background-color: #ffffff; }

.review__title {
  font-weight: 500;
  font-size: 30px;
  line-height: 37px;
  letter-spacing: -0.691667px;
  color: #2c2e3f;
  margin-left: 30px; }

.review__text {
  margin-left: 30px;
  font-size: 14px;
  line-height: 28px;
  letter-spacing: -0.408333px;
  color: rgba(44, 46, 63, 0.7); }

.review__author {
  font-family: Poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.466667px;
  color: #2c2e3f;
  margin-left: 100px; }

.reviews__wrapper {
  margin-top: -20px; }

.prices {
  display: flex;
  flex-direction: row; }

.prices__info {
  width: 299px;
  order: 1; }

.service-list {
  position: relative;
  background: #ffffff;
  border: 1px solid rgba(44, 46, 63, 0.3);
  box-sizing: border-box;
  border-radius: 6px;
  width: 370px;
  margin-right: 30px;
  margin-left: 10px; }

.service-list__title {
  font-weight: 600;
  font-size: 28px;
  line-height: 34px;
  display: flex;
  align-items: center;
  letter-spacing: -1.27273px;
  color: rgba(20, 21, 23, 0.7);
  margin-left: 30px; }

.service-list__cost {
  position: absolute;
  top: 30px;
  right: 30px;
  font-size: 25px;
  line-height: 34px;
  display: flex;
  font-weight: bold;
  align-items: center;
  text-align: right;
  color: rgba(20, 21, 23, 0.7); }

.service-list__text {
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: #2c2e3f;
  margin-left: 30px; }

.page-footer__wrapper {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: row; }
